.container {
  height: 507px;
  width: 100%;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative; }

.text, .closeButton {
  font-family: Doom;
  color: #db0000;
  font-size: 30px; }

.closeButton {
  position: absolute;
  top: 30px;
  right: 50px;
  cursor: pointer; }

.bigInput {
  background: url("../../assets/images/inputs/big_input.png") 0 0/470px 53px no-repeat;
  width: 470px;
  height: 53px;
  border: none;
  font-family: Doom;
  font-size: 28px;
  color: white;
  box-sizing: border-box;
  padding: 0 15px 4px 15px;
  text-shadow: 4px 2px black;
  transition: color linear 300ms; }
  .bigInput::placeholder {
    font-size: 20px; }
  .bigInput:hover {
    color: #fbab47; }



