.demon {
  width: 35px;
  height: 35px; }
  .demon.isKiller {
    background-color: #6b0000; }

.adjacentDemons {
  font-size: 24px;
  text-shadow: -1px 0 #000000,0 1px #000000,1px 0 #000000,0 -1px #000000; }
  .adjacentDemons.count1 {
    color: blue; }
  .adjacentDemons.count2 {
    color: green; }
  .adjacentDemons.count3 {
    color: red; }
  .adjacentDemons.count4 {
    color: purple; }
  .adjacentDemons.count5 {
    color: darkred; }
  .adjacentDemons.count6 {
    color: yellow; }
  .adjacentDemons.count7 {
    color: orange; }
  .adjacentDemons.count8 {
    color: pink; }

.wrongFlag {
  color: #6b0000;
  position: absolute;
  font-size: 18px;
  text-shadow: -1px 0 #ffffff,0 1px #ffffff,1px 0 #ffffff,0 -1px #ffffff; }



